"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "./card"
import { Checkbox } from "./checkbox"
import { Slider } from "./slider"
import { Label } from "./label"

export function ProductFilterSidebar() {
  const [selectedCategories, setSelectedCategories] = useState<string[]>(["All"])
  const [priceRange, setPriceRange] = useState([0, 1000])

  const categories = [
    { id: "all", label: "All" },
    { id: "electronics", label: "Electronics" },
    { id: "clothing", label: "Clothing" },
    { id: "home", label: "Home" }
  ]

  const handleCategoryChange = (categoryId: string, checked: boolean) => {
    if (categoryId === "all") {
      setSelectedCategories(checked ? ["All"] : [])
    } else {
      setSelectedCategories(prev => {
        const filtered = prev.filter(cat => cat !== "All")
        if (checked) {
          return [...filtered, categoryId]
        } else {
          return filtered.filter(cat => cat !== categoryId)
        }
      })
    }
  }

  return (
    <div className="w-64 space-y-6">
      {/* Main Filter Section */}
      <Card className="bg-blue-600 text-white">
        <CardHeader>
          <CardTitle className="text-white">Filters</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label className="text-white font-medium mb-3 block">Category</Label>
            <div className="space-y-2">
              {categories.map((category) => (
                <div key={category.id} className="flex items-center space-x-2">
                  <Checkbox
                    id={category.id}
                    checked={selectedCategories.includes(category.id === "all" ? "All" : category.id)}
                    onCheckedChange={(checked) => 
                      handleCategoryChange(category.id, checked as boolean)
                    }
                    className="border-white data-[state=checked]:bg-white data-[state=checked]:text-blue-600"
                  />
                  <Label 
                    htmlFor={category.id} 
                    className="text-white text-sm cursor-pointer"
                  >
                    {category.label}
                  </Label>
                </div>
              ))}
            </div>
          </div>

          <div>
            <Label className="text-white font-medium mb-3 block">Price</Label>
            <div className="space-y-3">
              <Slider
                value={priceRange}
                onValueChange={setPriceRange}
                max={1000}
                min={0}
                step={10}
                className="w-full"
              />
              <div className="flex justify-between text-sm text-white">
                <span>${priceRange[0]}</span>
                <span>${priceRange[1]}</span>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Secondary Filter Section */}
      <Card className="bg-gray-100">
        <CardHeader>
          <CardTitle className="text-gray-800">Category</CardTitle>
        </CardHeader>
        <CardContent className="space-y-2">
          <div className="flex items-center space-x-2">
            <Checkbox id="all2" defaultChecked />
            <Label htmlFor="all2" className="text-sm">All</Label>
          </div>
          <div className="flex items-center space-x-2">
            <Checkbox id="electronics2" />
            <Label htmlFor="electronics2" className="text-sm">Electronics</Label>
          </div>
          <div className="flex items-center space-x-2">
            <Checkbox id="clothing2" />
            <Label htmlFor="clothing2" className="text-sm">Clothing</Label>
          </div>
          <div className="flex items-center space-x-2">
            <Checkbox id="home2" />
            <Label htmlFor="home2" className="text-sm">Home</Label>
          </div>
        </CardContent>
      </Card>

      <Card className="bg-gray-100">
        <CardHeader>
          <CardTitle className="text-gray-800">Price</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            <Slider
              defaultValue={[500]}
              max={1000}
              min={0}
              step={10}
              className="w-full"
            />
            <div className="flex justify-between text-sm text-gray-600">
              <span>5000</span>
              <span>:</span>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
